/* Estilo para a barra de rolagem (scrollbar) */
.sidebar-scrollbar {
	scrollbar-width: thin; /* Para Firefox: define a largura da barra de rolagem como fina */
	scrollbar-color: #004b8d #6693bb; /* Cor do polegar e do fundo da barra */
}

.sidebar-scrollbar::-webkit-scrollbar {
	width: 8px; /* Largura da barra de rolagem */
}

.sidebar-scrollbar::-webkit-scrollbar-track {
	background: #6693bb; /* Cor de fundo da barra de rolagem */
}

.sidebar-scrollbar::-webkit-scrollbar-thumb {
	background-color: #004b8d; /* Cor do "polegar" da barra de rolagem */
	border-radius: 10px; /* Arredondamento do polegar da barra */
	border: 2px solid #6693bb; /* Adiciona uma borda para dar contraste */
}

.sidebar-scrollbar::-webkit-scrollbar-thumb:hover {
	background-color: #336d9a; /* Cor do polegar quando estiver com o mouse em cima */
}
