.topbar-container {
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: white;
	color: #004b8d;
	padding: 10px 20px;
	height: 80px;
}

.topbar-text {
	font-family: "Montserrat", sans-serif;
	margin: 0;
	font-size: 18px;
	font-weight: bold;
	cursor: pointer;
}

.topbar-icon {
	margin-right: 10px;
	vertical-align: middle;
	height: 20px;
}
